<template>
	<div class="search-container">
		<div class="search-header">
			<div class="search-xing">
				<img src="../assets/nav_back.png" @click="back()">
			</div>
			<div class="search-frame">
				<div class="search-sousuo">
					<input @keypress="searchGoods" type="serch" placeholder="搜索聊天套路" v-model="content">
				</div>
				<div class="search-picture">
					<img src="../assets/icon_search.png" @click="highlights()">
				</div>
			</div>
		</div>
		<div class="search-content">
			<!--<div class="search-huanchong" v-show="highlights_list.length == 0">
				<mu-circular-progress class="demo-circular-progress" :size="50"></mu-circular-progress>
			</div>-->
			<div class="search-huanchong" v-show="jiazai == 0">
				<mu-circular-progress color="#fd5997" class="demo-circular-progress" :size="50"></mu-circular-progress>
			</div>
			<div class="search-bj" v-for="item in page_list">
				<div v-if="data.user_vip == '3' || data.user_vip == '2' || data.user_vip == '1'">
					<div class="search-between">
						<div class="search-flex">
							<!--<div class="search-nv">
								<img src="../assets/icon_nv.png">
							</div>-->
							<div class="search-diyi">{{item.highlight_content}}</div>
						</div>
					</div>
					<div class="search-between" v-for="one in item.list">
						<div class="search-flex">
							<!--<div class="search-nv">
								<img src="../assets/icon_nan.png">
							</div>-->
							<div class="search-diyi">{{one.highcontent_content}}</div>
						</div>
						<div class="search-button" v-show="!!one.highcontent_highlight_id">
							<div class="search-fuzhi" v-clipboard:copy="one.highcontent_content" v-clipboard:success="onCopy" v-clipboard:error="onError">复制</div>
						</div>
					</div>
				</div>
				<div v-if="data.user_vip == '0'">
					<div class="search-between">
						<div class="search-flex">
							<div class="search-diyi">{{item.highlight_content}}</div>
						</div>
					</div>
					<div class="search-between" v-for="one in item.list">
						<div class="search-flex">
							<div class="search-diyi">{{one.highcontent_content}}</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="search-footer">

		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				highlights_list: '',
				toptagid: '',
				content: '',
				data: {},
				page_list: [],
				jiazai: ''
			}
		},
		methods: {
			back() {
				window.history.back();
			},
			//			专题内容搜索
			highlights() {
				var that = this;
				var params = {
					toptagid: that.toptagid,
					content: that.content
				};
				that.$http.post('/special/highlights', Qs.stringify(params), {

				}).then((res) => {
					that.jiazai = '0'
					if(res.meta.code == 200) {
						that.jiazai = '1',
						that.highlights_list = res.data;
						that.page_list = [];
						that.highlights_list.forEach(function(item, index) {

							if(that.data.user_vip == '0') {
								if(index < 10) {
									that.page_list.push(item);
								} else {
									that.page_list.push({
										highlight_content: '********',
										highlight_id: null,
										list: [{
											highcontent_content: '****请充值会员查看更多内容****',
											highcontent_highlight_id: null,
										}]
									});
								}
							} else {
								that.page_list.push(item);
							}
						})
						that.page_total = res.data.length;
					}else{
						that.$alert('查循内容为空', '提示');
						that.jiazai = '1'
					}
				})
			},
			//			个人信息
			userInfo() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
					//					discussionid: that.discussionid
				};
				that.$http.post('/user/userInfo', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
						//						that.highlights();
					}
				})
			},
			onCopy: function (e) {
				this.$toast.success('已复制');
      			// alert('You just copied: ' + e.text)
    		},
    		onError: function (e) {
      			// alert('Failed to copy texts')
    		},
			searchGoods(event) {
				var that = this;
				if(event.keyCode == 13) { 
					event.preventDefault();  
					// console.log(event.target.value)
					document.querySelector('input').blur();
//					Toast("点击了确认")
					this.highlights();
				}
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
			this.userInfo();
			this.content = this.$route.query.type;
			this.highlights()

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.search-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.search-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
	}
	
	.search-frame {
		border: 1px solid #f5f5f5;
		border-radius: 15px;
		background: #f5f5f5;
		width: 70%;
		height: 2.5rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-left: 6rem;
		position: absolute;
		top: 1rem;
	}
	
	.search-picture img {
		width: auto;
		height: 1.3rem;
		padding-left: 1rem;
		padding-right: 1.5rem;
		margin-top: 1.5rem;
	}
	
	.search-sousuo input {
		font-size: 1.4rem;
		height: 1.8rem;
		padding: 0.5rem 0;
		line-height: 1.8rem;
		width: 100%;
		color: #9fa0a0;
		border: none;
		outline: none;
		background: #f5f5f5;
		padding-left: 1rem;
		margin-left: 1rem;
	}
	
	.search-xing img {
		width: auto;
		height: 1.5rem;
		margin-top: 1.5rem;
		margin-left: 1rem;
	}
	
	.search-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
		/*background: #FFFFFF;*/
	}
	.search-huanchong{
		height: calc(100% - 50px);
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.search-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.search-bj {
		background: #FFFFFF;
		margin-bottom: 1.5rem;
	}
	
	.search-between {
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-top: 1px solid #EAEAEA;
	}
	
	.search-between1 {
		display: flex;
		justify-content: space-between;
		align-items: center;
		/*border-top: 1px solid #EAEAEA;*/
		width: 100%;
	}
	
	.search-flex {
		width: 0px;
		flex-grow: 1;
		display: flex;
		align-items: center;
	}
	
	.search-button {
		width: 80px;
		text-align: center;
	}
	
	.search-nv img {
		width: auto;
		height: 1.7rem;
		margin: 1.5rem 0.8rem 1.1rem 1.5rem;
	}
	
	.search-diyi {
		font-size: 1.4rem;
		color: #000000;
		padding: 0.5rem 1.5rem;
	}
	
	.search-fuzhi {
		font-size: 1.2rem;
		color: #FEFEFE;
		border: 1px solid #fd5d99;
		border-radius: 15px;
		background: #fd5d99;
		padding: 0.1rem 0.3rem;
		/*margin-right: 1rem;*/
		width: 60%;
		margin: 0 auto;
	}
	
	.search-dier {
		width: 0px;
		flex-grow: 1;
		box-sizing: border-box;
		font-size: 1.4rem;
		color: #000000;
		padding: 1.5rem 0 1.5rem 4rem;
	}
</style>


